<template>
	<div id="background">
		<div id="messages">
			<Row class="hidden">dfadasd</Row>
			<Row>
				<Col span="1">
					<div  @click="clickLeft">
						<Icon type="chevron-left" size="22" class="cursor">
					</Icon>
					</div>
				</Col>
				<Col span="22">
					<p>{{messages[currentMsg]}}</p>
				</Col>
				<Col span="1">
					<div  @click="clickRight">
						<Icon type="chevron-right" size="22" class="cursor" @click="clickRight">
					</Icon>
				</div>
				</Col>
			</Row>
		</div>
		<div id="others">
			
		</div>
		<div id="groups">
			<div id="groups-title">
				<strong>经常</strong>访问
			</div>
			<Table :row-class-name="rowClassName" :columns="columns" :data="tableData"></Table>
		</div>
	</div>
</template>
<script>
export default {
props: ['username'],
methods: {
	clickLeft: function() {
		if(this.currentMsg == 0) {
			return
		}
		this.currentMsg--
	},
	clickRight: function() {
		if(this.currentMsg == (this.messages.length - 1)) {
			return
		}
		this.currentMsg++
	},
	rowClassName: function(row, index) {
		if (index === 1) {
            return 'demo-table-info-row';
        } else if (index === 3) {
            return 'demo-table-error-row';
        }
        return '';
	}
},
data: function() {
	return {
		currentMsg: 0,
		messages: [
			'隔壁超市的薯片半价啦',
			'隔壁家的狗生小狗啦',
			'对不起我编不下去啦',
		],
		columns: [
			{
				title: '小组编号',
				key: 'group_id',

			},
			{
				title: '小组名',
				key: 'group_name',

			},
			{
	            title: '操作',
	            key: 'action',
	            width: 200,
	            align: 'center',
	            render: (h, params) => {
	                return h('div', [
	                    h('Button', {
	                        props: {
	                            type: 'primary',
	                            size: 'small'
	                        },
	                        style: {
	                            marginRight: '5px'
	                        },
	                        on: {
	                            click: () => {
	                                // this.show(params.index)
	                            }
	                        }
	                    }, '进入小组'),
	                    h('Button', {
	                        props: {
	                            type: 'error',
	                            size: 'small'
	                        },
	                        on: {
	                            click: () => {
	                                // this.remove(params.index)
	                            }
	                        }
	                    }, '退出小组')
	                ]);
	            }
	        }
		],
		tableData: [
			{
				group_id: '1',
				group_name: '测试小组1'
			},
			{
				group_id: '2',
				group_name: '测试小组2'
			},
			{
				group_id: '3',
				group_name: '测试小组3'
			},
			{
				group_id: '4',
				group_name: '测试小组4'
			},
			{
				group_id: '5',
				group_name: '测试小组5'
			},
		]

	}
}
}
</script>
<style>
#messages, #groups, #others {
  width: 90%;
  height: 70px;
  margin: 0 auto;
  margin-top: 20px;
  background-color: #FFFFFF;
  box-shadow:0 1px 2px rgba(0,0,0,0.3);
  border-radius: 5px;
  position: relative;
}
#others {
	height: 100px;
	background: -webkit-linear-gradient(left top,#2d8cf0,#66f9cf);
}
#groups-title {
	font-size: 16px;
}
#groups {
	height: 500px;
}
#messages p {
	font-size: 22px;
}
.hidden {
	visibility: hidden;
}
.cursor:hover {
	cursor: pointer;
}
.ivu-table .demo-table-info-row td{
    background-color: #2db7f5;
    color: #fff;
}
.ivu-table .demo-table-error-row td{
    background-color: #ff6600;
    color: #fff;
    }
</style>


