<template>
  <div id="background">
    <div id="messages-box">
      <Collapse value="1" accordion>
        <Panel v-for="msg of messageList">
            {{msg.title}}
            <p slot="content">{{msg.messges}}</p>
        </Panel>
      </Collapse>
      <div id="page">
        <Page :total="totalPage" @on-change="changePage"></Page>
      </div>
    </div>
  </div>
</template>
<script>
import router from '../../router'
import { getMessages } from '../../api/api'
export default {
  mounted: function() {
    let options = {
      pageNum: 1,
      pageSize: 10,
    }
    getMessages(options)
      .then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            this.messageList = res.data.data.list
            this.totalPage = res.data.data.total

          } else {
            this.$Message.info("未知错误")
          }
        } else {
          this.$Message.info(`未知错误，状态码:${res.status}`)
        }
      })
  },
  data: function() {
    return {
      messageList: [],
      totalPage: 0,
    }
  },
  methods: {
    changePage: function(num) {
      let options = {
        pageNum: num,
        pageSize: 10,
      }
      getMessages(options)
        .then(res=>{
          if(res.status == 200) {
            if(res.data.status == 1) {
              console.log(res.data.data.list)
              this.messageList = res.data.data.list
              this.totalPage = res.data.data.total

            } else {
              this.$Message.info("未知错误")
            }
          } else {
            this.$Message.info(`未知错误，状态码:${res.status}`)
          }
        })
    }
  }
}
</script>
<style>
div {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
#messages-box {
  width: 90%;
  /*height: 600px;*/
  margin: 0 auto;
  margin-top: 30px;
  
}
#page {
  margin-top: 10px;
}
</style>


